<template>
  <div>
    <navDischarge/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Registro de Alta</b>
      </h1>
      <form @submit="save">
        <div class="col-md-3">
          <p class="p-2">Abono</p>
        </div>
        <div class="form-row p-3">
          <div class="col-md-3">
            <input
              class="form-control"
              placeholder="Abono"
              v-model.number="abono"
              type="text"
              required
            >
          </div>
          <div class="col-md-3">
            <button class="btn btn-success">Dar de Alta</button>
          </div>
        </div>
        <h5 class="p-5 text-dark text-center">
          <b>Listado de Internaciones</b>
        </h5>
        <b-row>
          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
              <b-input-group>
                <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
                <b-input-group-append>
                  <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
              <b-input-group>
                <b-form-select v-model="sortBy" :options="sortOptions">
                  <option slot="first" :value="null">-- Seleccione --</option>
                </b-form-select>
                <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                  <option :value="false">Asc</option>
                  <option :value="true">Desc</option>
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
        </b-row>
        <b-table
          id="table-internment"
          show-empty
          selectable
          :select-mode="selectMode"
          selectedVariant="success"
          hover
          small
          stacked="xl"
          fixed
          empty-filtered-text="No se encontrarón registros en su búsqueda"
          empty-text="No hay datos para mostrar"
          :items="internments"
          :fields="columns"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          :current-page="currentPage"
          :per-page="10"
          @filtered="onFiltered"
          @row-selected="rowSelected"
        >
          <template slot="paciente" slot-scope="data">{{data.value.nombre}} {{data.value.apellido}}</template>
          <template
            slot="medico"
            slot-scope="data"
          >{{data.value.nombre}} {{data.value.apellido}} {{data.value.especialidad}}</template>
          <template slot="piso" slot-scope="data">{{data.item.habitacion.piso.descripcion}}</template>
          <template slot="numero" slot-scope="data">{{data.item.habitacion.numero}}</template>
          <template
            slot="fechaInternacion"
            slot-scope="data"
          >{{convertDateToString(data.item.fechaInternacion)}}</template>
          <template slot="estadoInternacion" slot-scope="data">
            <span
              v-if="data.item.estadoInternacion.descripcion==='En progreso'"
              class="btn btn-success"
            >{{data.item.estadoInternacion.descripcion}}</span>
            <span v-else class="btn btn-danger">{{data.item.estadoInternacion.descripcion}}</span>
          </template>
        </b-table>
        <b-row>
          <b-col md="6" class="my-1">
            <b-pagination
              size="md"
              :total-rows="totalRows"
              v-model="currentPage"
              :per-page="10"
              @input="find(currentPage)"
            ></b-pagination>
          </b-col>
        </b-row>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navDischarge from "@/components/discharge/navDischarge.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "darAlta",
  data() {
    return {
      columns: [
        {
          key: "paciente",
          label: "Paciente",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "medico",
          label: "Médico",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "piso",
          label: "Piso"
        },
        {
          key: "numero",
          label: "Número"
        },
        {
          key: "motivo"
        },
        {
          key: "fechaInternacion",
          label: "Fecha de Ingreso"
        },
        {
          key: "estadoInternacion",
          label: "Estado"
        }
      ],
      internments: [],
      internment:[],
      selected: [],
      internacion: {
        id: null
      },
      abono: null,
      newAbono: null,
      fechaSalida: null,
      selectMode: "single",
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      totalRows:null,
      currentPage:1
    };
  },
  methods: {
    async findAll() {
      axios
        .get(`${URL}/api/internaciones`)
        .then(response => {
          this.internments = response.data;
          this.internments = this.internments.filter(internment => {
            if (internment.estadoInternacion.descripcion === "En progreso") {
              return internment;
            }
          });
        })
        .catch(error => console.log(error.message));
    },
    async find(currentPage) {
      axios
        .get(`${URL}/api/internacion?page=${currentPage-1}`)
        .then(response => {
          this.internment = response.data.content;
          this.totalRows=response.data.totalElements
          this.internment = this.internment.filter(internment => {
            if (internment.estadoInternacion.descripcion === "En progreso") {
              return internment;
            }
          });
        })
        .catch(error => console.log(error.message));
    },
    save(e) {
      e.preventDefault();
      axios
        .post(`${URL}/api/alta`, {
          internacion: this.internacion,
          abono: Math.round(this.abono * 100) / 100,
          fechaSalida: new Date()
        })
        .then(response => {
          this.internacion = {};
          this.abono = null;
          this.fechaSalida = null;
          bootbox.alert({
            title: "Datos Guardados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/altas")
          });
        })
        .catch(error => {
          if(error.response.status===404){
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error " + error.response.status + " Not Found");
          }
          if(error.response.status===409){
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error " + error.response.status + " Not Found");
          }
        });
    },
    convertDateToString(date) {
      let dateObject = new Date(date);
      let formatter = new Intl.DateTimeFormat("es", {
        year: "numeric",
        month: "long",
        day: "numeric"
      });
      return formatter.format(dateObject);
    },
    rowSelected(items) {
      this.selected = items;
      for (let internacion of this.selected) {
        this.internacion = { id: internacion.id };
      }
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  mounted(currentPage){
    this.find(currentPage)
    this.findAll()
  },
  components: {
    navDischarge,
    Footer
  }
};
</script>

